// .social {}

.social__list {
  display: flex;
  justify-content: space-between;

  width: 132px;
  margin-bottom: 43px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}

// .social__item {}

// .social__link {}

.social__icon {
  display: block;
  fill: #666666;
}

.social__icon--vkontakte,
.social__icon--youtube,
.social__icon--telegram {
  &:hover {
    fill: @green;
  }

  &:active {
    fill: @green;
    opacity: 30%;
  }
}
